import validator from 'validator';

// Validao por parte do Front-end.
// O Back-end tambm vai fazer essas validaes, mas aqui  outra forma de validar que  mais simples:
/*
* Essa validao ela  mais otimizada por evita requisies desnecessrias para o servidor, onde em vez
* de eu enviar o formulrio e ento obter a informao dos dados presentes neles estarem invlidos pelo Back-end,
* eu nem faria esse envio, logo poupando recursos fazendo que o front-end j estivese preparado para lidar com esses
* erros e otimizando o tempo de resposta e simplificando a construo da minha aplicao.
*/
export default class Login {
    constructor(formClass) {
        this.form = document.querySelector(formClass);
    }

    init() {
        this.events();
    }

    events() {
        if (!this.form) return;
        this.form.addEventListener('submit', e => {
            e.preventDefault();
            this.validate(e);
        });
    }

    // ! Idealmente isso seria feito por uma mensagem elemento em HTML e no por um alert()
    validate(e) {
        const el = e.target;
        const emailInput = el.querySelector('input[name="email"]');
        const passwordInput = el.querySelector('input[name="password"]');
        let error = false;

        if (!validator.isEmail(emailInput.value)) {
            alert('Email invlido');
            error = true;
        }

        if (passwordInput.value.length < 3 || passwordInput.value.length >= 50) {
            alert('Senha precisa ter entre 3 e 50 caracteres');
            error = true;
        }

        if (!error) el.submit();
    }
}